}

.images > img:nth-child(5) {
  padding-right: 12em;
}

#checkbox-menu {
  position: absolute;
  opacity: 0%;
}

.hamburguer-lines {
  cursor: pointer;
  position: relative;
  display: block;
  height: 18px;
  width: 19px;
}

.hamburguer-lines > span {
  position: absolute;
  display: block;
  height: 2px;
  width: 100%;
  background-color: #4C3B92;
  transition: 0.25s ease-in-out;
}

.hamburguer-lines > span:nth-child(1) {
  top: 0;
}

.hamburguer-lines > span:nth-child(2) {
  top: 6px;
}

.hamburguer-lines > span:nth-child(3) {
  top: 12px;
}

#checkbox-menu:checked + .hamburguer > .hamburguer-lines > span:nth-child(1) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  top: 6px;
}

#checkbox-menu:checked + .hamburguer > .hamburguer-lines > span:nth-child(2) {
  opacity: 0;
}

#checkbox-menu:checked + .hamburguer > .hamburguer-lines > span:nth-child(3) {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 6px;
}

